<template>
    <div v-if="NeedToWaitNotification" class="notification is-warning">
        <strong>Please Wait!</strong> Please wait! The page is freeplan hosted, it needs to wake up first, this will take about 40 seconds.
    </div>
    <h1>{{ emoji }}</h1>
    <div class="container">
        <video
            src=""
            id="video"
            width="720"
            height="560"
            autoplay
            @play="faceDetection"
        ></video>
    </div>
</template>

<script>
    import * as faceapi from "face-api.js";
    export default {
        name: "HelloWorld",
        data() {
            return {
                expression: "",
                emoji: "",
                NeedToWaitNotification:true
            };
        },
        methods: {
            loadModules() {
                faceapi.nets.tinyFaceDetector.loadFromUri("./weights");
                faceapi.nets.faceLandmark68Net.loadFromUri("./weights");
                faceapi.nets.faceRecognitionNet.loadFromUri("./weights");
                faceapi.nets.faceExpressionNet.loadFromUri("./weights");
                faceapi.nets.ageGenderNet.loadFromUri("./weights");
            },
            pleaseWaitNotification() {
                setTimeout(() => {
                    this.NeedToWaitNotification = false;
                }, 40000);
            },
            startVideo() {
                navigator.getUserMedia =
                    navigator.getUserMedia ||
                    navigator.webkitGetUserMedia ||
                    navigator.mozGetUserMedia ||
                    navigator.msGetUserMedia;
                const video = document.querySelector("video");
                navigator.getUserMedia(
                    {
                        video: {},
                    },
                    (stream) => (video.srcObject = stream),
                    (err) => console.error(err)
                );
            },
            getExpression(e) {
                let sortable = [];
                for (var expression in e) {
                    sortable.push([expression, e[expression]]);
                }
                sortable.sort(function (a, b) {
                    return b[1] - a[1];
                });
                if (sortable[0][0]) this.expression = sortable[0][0];
                else this.expression = "neutral";
               
            },
            faceDetection() {
                const video = document.querySelector("video");
                const container = document.querySelector(".container");
                const canvas = faceapi.createCanvasFromMedia(video);
                container.append(canvas);
                const displaySize = {
                    width: video.width,
                    height: video.height,
                };
                faceapi.matchDimensions(canvas, displaySize);
                setInterval(async () => {
                    const detections = await faceapi
                        .detectAllFaces(
                            video,
                            new faceapi.TinyFaceDetectorOptions()
                        )
                        .withFaceLandmarks()
                        .withFaceExpressions()
                        .withAgeAndGender();


                    console.log(detections);
                    if (detections[0]) this.getExpression(detections[0].expressions);

                    const resizedDetections = faceapi.resizeResults(
                        detections,
                        displaySize
                    );
                    canvas
                        .getContext("2d")
                        .clearRect(0, 0, canvas.width, canvas.height);
                    faceapi.draw.drawDetections(canvas, resizedDetections);
                    faceapi.draw.drawFaceExpressions(canvas, resizedDetections);
                    resizedDetections.forEach((detection) => {
                        const box = detection.detection.box;
                        const drawBox = new faceapi.draw.DrawBox(box, {
                            label:
                                detection.gender +
                                ", environ " +
                                Math.round(detection.age) +
                                " ans ",
                        });
                        drawBox.draw(canvas);
                    });
                }, 400);
            },
        },
        watch: {
            expression: function (val) {
                console.log(val);
                switch (val) {
                    case "angry":
                        this.emoji = "😠";
                        break;
                    case "disgusted":
                        this.emoji = "😖";
                        break;
                    case "fearful":
                        this.emoji = "😨";
                        break;
                    case "happy":
                        this.emoji = "😃";
                        break;
                    case "neutral":
                        this.emoji = "😐";
                        break;
                    case "sad":
                        this.emoji = "🥺";
                        break;
                    case "surprised":
                        this.emoji = "😮";
                        break;

                    default:
                        this.emoji = "😐";
                        break;
                }
            },
        },
        mounted() {
            this.startVideo();
            this.pleaseWaitNotification();
        },
        beforeMount() {
            this.loadModules();
        },
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
    h1 {
        position: absolute;
        top: 10%;
        left: 45%;
        font-size: 100px;
    }

    .container {
        margin: 0;
        padding: 0;
        width: 100vw;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    canvas {
        position: absolute;
    }
    .notification{

        border: 4px solid violet;
        padding: 10px;
        margin-block: 10px;
        margin-inline: auto;
        border-radius: 10px;
        width: fit-content;
        box-shadow: 10px 10px 5px #aaaaaa;

    }
</style>
